@import url(https://fonts.googleapis.com/css2?family=Balsamiq+Sans&family=Roboto&display=swap);
*{margin:0;padding:0;box-sizing:border-box;font-family:"Balsamiq Sans","roboto","arial"}:root{font-size:62.5%}#root{height:100vh;width:100vw;min-width:37.5rem;min-height:56.8rem;display:flex;flex-direction:column;flex-wrap:nowrap;justify-content:center;align-items:center;align-content:center;background-color:#0ca14f}.wrapper{height:100%;width:100%;max-height:90rem;min-height:56.8rem;min-width:37.5rem;display:flex;flex-direction:column;flex-wrap:nowrap;justify-content:space-around;align-items:center;align-content:center}.wrapper .hangman{height:40%;display:flex;flex-direction:row;flex-wrap:nowrap;justify-content:center;align-items:center;align-content:center}.wrapper .random-word{display:flex;flex-direction:row;flex-wrap:nowrap;justify-content:center;align-items:center;align-content:center}.wrapper .random-word__list{height:50%;display:flex;flex-direction:row;flex-wrap:nowrap;justify-content:center;align-items:center;align-content:center;list-style-type:none}.wrapper .random-word__letter{height:3.5rem;font-size:3rem;width:10%;margin:0 0.6rem;text-align:center}@media only screen and (min-width: 568px){.wrapper .random-word__letter{margin:0 1rem;font-size:4rem}}.wrapper .input{display:flex;flex-direction:row;flex-wrap:wrap;justify-content:center;align-items:center;align-content:flex-start;margin:0 0.3rem}@media only screen and (min-width: 568px){.wrapper .input{width:35rem}}.wrapper .input__letter{width:3.5rem;height:3.5rem;font-size:2.5rem;padding:0.5rem;margin:0.5rem;border-radius:0.5rem;background-color:#fff;box-shadow:0.5rem 0.3rem black;text-align:center;line-height:3rem;transition:0.2s}.wrapper .input__letter:hover{opacity:0.5}.wrapper .input__letter--correct{background-color:#0ca14f;pointer-events:none}.wrapper .input__letter--correct:hover{opacity:1}.wrapper .input__letter--incorrect{background-color:#af1212;pointer-events:none}.wrapper .input__letter--incorrect:hover{opacity:1}.modal{height:100vh;max-height:90rem;min-height:56.8rem;min-width:37.5rem;font-size:2rem;display:flex;flex-direction:column;flex-wrap:nowrap;justify-content:center;align-items:center;align-content:center}.modal__game-state{font-size:5rem;text-align:center;margin-bottom:2.5rem}.modal__text,.modal__answer{text-align:center}.modal__answer{font-size:3.5rem;margin-top:1rem;margin:1rem 0 2.5rem 0}.modal__btn{width:15rem;height:5rem;font-size:2rem;background-color:#fff;border-radius:0.5rem;border:none;transition:0.25s;box-shadow:0.5rem 0.3rem black}.modal__btn:hover{transform:translate(0, -0.5rem)}

